*{
  color: #333333;
}

html, body{
  box-sizing: border-box;
}
.side-nav-container{
  max-width:300px;
  height:100%;
  padding:10px;
  position:fixed;
  top:0;
  left:0;
  background-color:#ebf5f1;
}

.side-nav{
  width:100%;
  height:100%;
  overflow-y: scroll;
  background-color:#ebf5f1;
}